.about__top-wrapper{
    display: flex;
}
.about__top-content{
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    max-width: 425px;
    width: 100%;
    gap: 25px;
    line-height: 208%;
    margin-top: 93px;
    margin-bottom: 144px;
}
.about__top-title{
    font-size: 30px;
    font-weight: 700;
}
.about__top-paragraph{
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 25px;
}
.about__top-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 16px 16px 19px;
    max-width: 176px;
    gap: 18px;
    border-radius: 40px;
    margin-bottom: 116px;
    background: linear-gradient(113deg, #FA8305 0%, #FB9E3C 100%);
     
}
.about__top-btn:hover{
    background: linear-gradient(113deg, #7a4003 0%, #905a23 100%);
    transition-duration: 5s;
    transition-property: background;
    transition-timing-function: linear;
}

.swiper{
    max-width: 739px;
    position: absolute;
    right:0;
}

.swiper-wrapper.swiper-wrapper .slider__about-img {
    width: 100%;
    max-height: 649px;
    height: 100%;
}
.swiper-pagination{
    position: absolute;
    bottom: 0;
    left: 0;
   max-width: 179px;
    height: 67px;
    flex-shrink: 0;
    background: #333;
    display: flex;
    align-items: center;
    justify-content: space-between;
   padding: 17px 70px;
}
.swiper-button-next::after {
    position: absolute;
    content: url("../../images/arrow6.svg");
    bottom: 87px;
    left: 50px;
    width: 12px;
    height: 12px;
}
.swiper-button-next {
    position: absolute;
    top: 106%;
    left: 70px;
    width: 12px;
    height: 13px;
    z-index: 11;
}
.swiper-button-prev::after {
    position: absolute;
    content: url("../../images/arrow7.svg");
    bottom: 87px;
    left: 17px;
    width: 12px;
    height: 12px;
}
.swiper-button-prev {
    position: absolute; 
    top: 106%;  
    left: 25px;
    width: 12px;
    height: 13px;
    z-index: 11;
}

.about__info-wrapper{
    display: flex;
    flex-direction: column;
    gap: 63px;
}
.about__info-title{
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    line-height: 180.5%;
}
.about__cards-wrapper{
    display: grid;
    grid-template-columns: repeat(3, minmax(378px, 1fr));
    gap: 43px;
   margin-bottom: 143px;
}
.about__card{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 37px 64px;
    gap: 19px;
    border-radius: 20px;
    background: #1C140F;
    box-shadow: 0px 4px 94px 0px rgba(0, 0, 0, 0.15);

}
.about__card-title{
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 187.5%;
    margin-bottom: 15px;
}
.about__card-paragraph{
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 187.5%; 
}
@media screen and (min-width:1920px) {
    .swiper{
        right:20%;
    }
}