.middle{
    display: flex;
    flex-direction: column;
    gap: 190px;
}
.middle__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.middle__wrap div{
    max-width: 536px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    line-height: normal;
}
.middle__wrap:nth-child(2){
    flex-direction: row-reverse;
}
.middle__wrap:nth-child(3){
    margin-bottom: 168px;
}
.middle__wrap h2{
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 43px;
}
.middle__wrap p,
.middle__wrap span{
    font-size: 17px;
    font-weight: 400;
}
.middle__wrap p{
    margin-bottom: 18px;
}
.middle__wrap span{
    margin-bottom: 46px;
}
.middle__wrap button{
    max-width: 177px;
    height: 50px;
    flex-shrink: 0;
    background: #DC7000;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 13px 44px;
}

