.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 88px;
    z-index: 5;
    position: relative;
    /* max-width: 1415px; */
    
}
.logo{
    width: fit-content;
    font-size: 29px;
    font-weight: 800;
    line-height: 44px;
}
.nav{
    display: flex;
    gap: 35px;
}
.header__list{
    display: flex;
    align-items: center;
    width: 100%;
   position: relative;
   gap: 88px;
   padding: 0;
}
.header__list-link{
    display: flex;
    font-size: 1rem;
    font-weight: 400;
    line-height: 24px;
    width: 100%;
    padding: 0;
    min-width: fit-content;
    position: relative;
}
.hoverAffect{
    width: 39px;
    position: absolute;
    bottom: -9px;
    left: -40%; 
    transition: all .75s;
}

.header__list-link:nth-last-child(1){
    align-items: center;
    justify-content: center;
    max-width: 132px;
    width: 100%;
    font-size: 13px;
    line-height: 19px;
    border-radius: 6px;
    padding: 11px 28px;
    background-color: #FA9021;
}

.contact:hover{
    background: #7d4911;
    transition: .75s ease-in;
}