.form{
    width: 100%;
    max-width: 385px;
    position: relative;
    padding-left: 10px;
}
.form:focus{
    border:none;
}

.form__wrap{
    position: relative;
}

.form__email-input{
    width: 100%;
    max-width: 385px;
    height: 48px;
    border-radius: 8px;
    background: #FFF;
    padding-left: 10px;
    font-size: 1rem;
    border: none;
}
.form__email-input::placeholder{
    color: #898989;
    text-align: start;
    font-family: Poppins;
    font-size: 13px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    display: flex;
    align-items: center;
    padding-left: 20px;
    border: none;
}
input[type="email"]:focus {
    border: none;
     outline: 0;
    border-width: thin;
    padding-right: 10px;
}
.form__email-input:target{
    padding: 10px;
}
.form__email-sub{
    position: absolute;
    right: 6px;
    top: 5px;
    width: 104.996px;
    height: 37px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    background: #DC7000;
}
