.bottom__loop{
    display: flex;
    flex-direction: column;
    max-width: 545px;
    width: 100%;
    margin-bottom: 74px;
}
.bottom__title{
    font-size: 30px;
    font-weight: 600;
    line-height: 187.5%;
    margin-bottom: 10px;
}
.loop__paragraph{
    font-size: 16px;
    font-weight: 400;
    line-height: 187.5%;
    
}
.bottom__form-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 163px;
    padding: 40px 56px;
    border-radius: 15px;
    background: #1C140F;
}
.form__title{
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 10px;
}
.form__paragraph{
    display: flex;
    max-width: 462px;
    width: 100%;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 187%;
}